<template>
  <div style="width:100%;">
    <div class="block">
      <p>默认</p>
      <el-date-picker
        v-model="value1"
        type="date"
        placeholder="选择日期">
      </el-date-picker>
    </div>
    <div class="block">
      <p>带快捷选项</p>
      <el-date-picker
        v-model="value2"
        align="right"
        type="date"
        placeholder="选择日期"
        :picker-options="pickerOptions1">
      </el-date-picker>
    </div>
    <div class="block">
      <p>周</p>
      <el-date-picker
        v-model="value3"
        type="week"
        format="yyyy 第 WW 周"
        placeholder="选择周">
      </el-date-picker>
    </div>
    <div class="block">
      <p>月</p>
      <el-date-picker
        v-model="value4"
        type="month"
        placeholder="选择月">
      </el-date-picker>
    </div>
    <div class="block">
      <p>年</p>
      <el-date-picker
        v-model="value5"
        type="year"
        placeholder="选择年">
      </el-date-picker>
    </div>
    <div class="block">
      <p>多个日期</p>
      <el-date-picker
        type="dates"
        v-model="value14"
        placeholder="选择一个或多个日期">
      </el-date-picker>
    </div>
    <div class="block" style="width: 30%;">
      <p>默认</p>
      {{value6}}
      <el-date-picker
        v-model="value6"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
    </div>
    <div class="block" style="width: 30%;">
      <p>带快捷选项</p>
      <el-date-picker
        v-model="value7"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        :picker-options="pickerOptions2">
      </el-date-picker>
    </div>
    <div class="block" style="width: 30%;">
      <p>默认为 Date 对象</p>
      <p>值：{{ value10 }}</p>
      <el-date-picker
        v-model="value10"
        type="date"
        placeholder="选择日期"
        format="yyyy 年 MM 月 dd 日">
      </el-date-picker>
    </div>
    <div class="block" style="width: 30%;">
      <p>使用 value-format</p>
      <p>值：{{ value11 }}</p>
      <el-date-picker
        v-model="value11"
        type="date"
        placeholder="选择日期"
        format="yyyy 年 MM 月 dd 日"
        value-format="yyyy-MM-dd">
      </el-date-picker>
    </div>
    <div class="block" style="width: 30%;">
      <p>时间戳</p>
      <p>值：{{ value12 }}</p>
      <el-date-picker
        v-model="value12"
        type="date"
        placeholder="选择日期"
        format="yyyy 年 MM 月 dd 日"
        value-format="timestamp">
      </el-date-picker>
    </div>
    <div>
      <p>单个控制时间选择器</p>
      <el-date-picker
        v-model="value8"
        type="date"
        placeholder="选择日期"
        :picker-options="pickerOptions3"
      ></el-date-picker>
    </div>
    <div>
      <p>两个时间控件相互影响</p>
      <el-date-picker
        v-model="value15"
        type="date"
        placeholder="选择日期"
        :picker-options="pickerOptions4"
      ></el-date-picker>
      <el-date-picker
        v-model="value16"
        type="date"
        placeholder="选择日期"
        :picker-options="pickerOptions5"
      ></el-date-picker>
    </div>
  </div>
</template>

<script>
export default {
  name: 'data',
  data () {
    return {
      pickerOptions1: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      },
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      pickerOptions3: {
        disabledDate (time) {
          return time.getTime() > Date.now() || time.getTime() < new Date(new Date() - 1000 * 60 * 60 * 24 * 30)
        }
      },
      pickerOptions4: this.pickerOpt4(),
      pickerOptions5: this.pickerOpt5(),
      value1: '',
      value2: '',
      value3: '',
      value4: '',
      value5: '',
      value6: '',
      value7: '',
      value8: '',
      value10: '',
      value11: '',
      value12: '',
      value14: '',
      value15: '',
      value16: ''
    }
  },
  methods: {
    pickerOpt4 () {
      let self = this
      return {
        disabledDate (time) {
          return time.getTime() > new Date(self.value16).getTime()
        }
      }
    },
    pickerOpt5 () {
      let self = this
      return {
        disabledDate (time) {
          return time.getTime() < new Date(self.value15).getTime()
        }
      }
    }
  }
}
</script>

<style scoped>
div{
  width: 20%;
  float: left;
}
p{
  text-align: left;
}
  /*span{*/
    /*text-align:center;*/
    /*color:blue;*/
    /*float: left;*/
  /*}*/
</style>



